.container {
  border-top: 1px solid $grey;
  border-bottom: 1px solid $black;
  background-color: $gray;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
  grid-template-areas: "header" "body" "footer";
  column-gap: 16px;
  row-gap: 8px;
  padding: 16px;

  &__header {
    background-color: $yellow;
    box-shadow: $shadow-3dp;
    color: $black;
    grid-area: header;
    padding: 8px 16px 8px 16px;
    margin: 8px 0;
    margin-left: -16px; }

  &__body {
    color: $yellow;
    grid-area: body; }

  &__footer {
    grid-area: footer;
    justify-self: end;
    margin: 8px 0; }

  &__block {
    margin-bottom: 16px; } }
